@import url(https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,400;0,500;0,700;1,300&display=swap);
.app{background-image:url(/weather_app_v2/static/media/cold.64fc9066.svg);background-size:cover;background-position:center;transition:0.45s ease-out;box-shadow:0px 0px 40px 0px #19193d}.app-filter{max-width:100%;width:100%;margin:0 auto;overflow-x:hidden;background-image:linear-gradient(to bottom, rgba(0,0,0,0.2), rgba(0,0,0,0.7));border-radius:10px}.main{height:99vh;padding:25px;border-radius:10px}.search-box{width:100%;margin:0 0 75px}.search-bar{font-size:20px;color:#313131;width:100%;padding:15px;border:none;border-radius:0px 0px 16px 16px;outline:none;background-color:rgba(255,255,255,0.5);box-shadow:0px 5px rgba(0,0,0,0.2);transition:0.4s ease}.search-bar:focus{background-color:rgba(255,255,255,0.75)}.location{font-size:55px;font-weight:500;text-align:center;text-shadow:3px 3px rgba(50,50,70,0.5);visibility:hidden}.date{display:flex;font-size:20px;font-weight:300;padding-top:5px;font-style:italic;justify-content:center;text-shadow:2px 2px rgba 50,50,70,0.5;visibility:hidden;text-align:center}.weather{text-shadow:3px 6px rgba(50,50,70,0.5);margin:0 auto;margin-top:50px;background-color:rgba(255,255,255,0.2);border-radius:16px;padding:15px 18px 15px 18px;box-shadow:3px 6px rgba(0,0,0,0.2);display:flex;justify-content:center;flex-direction:column;font-size:48px;font-weight:700}.weather-and-svg{font-size:15px;font-weight:700;text-shadow:3px 2px rgba(50,50,70,0.5);display:flex;align-items:right;justify-content:right;justify-self:right;justify-items:right;float:right}.background-auto-size{display:flex}.weather-icon{stroke:black;fill:white;display:flex;visibility:hidden;align-items:center;justify-content:center;width:100px}.main-forecast-info{height:125px;display:flex;flex-direction:column;padding-left:8px;font-size:23px;align-items:left;text-align:right;float:left;justify-content:center;visibility:hidden}.full{display:flex;text-align:left}.main-temp{font-size:100px;visibility:hidden;width:100%;display:flex;justify-content:center;text-align:center;align-items:center}.weather-condition{display:flex;text-align:left;font-size:33px;text-transform:capitalize;text-shadow:2px 2.5px rgba(50,50,70,0.5)}.forecast{padding-top:150px;padding-bottom:20px;display:flex;flex-direction:row;margin:auto}.forecast svg{fill:white;stroke:black;width:80px}.forecast-icon{width:100%;justify-content:center;margin-left:4px}.forecast-date{display:flex;width:100%;justify-content:center;align-items:center;text-align:left}.forecast-temps{font-size:18px;width:100%;display:flex;justify-content:center}.day-forecast{justify-content:center;text-align:center;line-height:28px;padding-right:10px;padding-left:10px;flex:1 1;word-break:break-word;font-size:18px;visibility:hidden}.min-temp{color:#bababa}@media screen and (min-width: 1700px){.app,.app-filter,.main{width:550px;height:950px}}@media screen and (min-width: 800px) and (max-width: 1700px) and (min-width: 600px){.app,.app-filter,.main{width:550px;height:850px}.forecast{padding-top:100px}}@media screen and (min-width: 700px) and (max-width: 800px) and (min-width: 600px){.app,.app-filter,.main{width:550px;height:850px;border-radius:10px}.forecast{padding-top:100px}}@media screen and (min-width: 700px){.app{border-radius:10px}.desktop{background:linear-gradient(to bottom, #07193a, #1363e9);display:flex;justify-content:center;align-items:center;background-size:cover;background-position:bottom;min-height:100vh;padding-top:0.5vh;padding-bottom:0.5vh}}@media (max-width: 700px){.main{min-height:100vh;box-shadow:none;border-radius:0}.app-filter{border-radius:0}}@media (max-width: 500px){.location{font-size:50px}.date{font-size:19px}.weather-condition{font-size:30px}.main-forecast-info{font-size:20px}.main-temp{font-size:85px}.forecast{padding-top:100px}}@media (max-width: 370px){.location{font-size:45px}.date{font-size:18px}.weather{padding:0px 1px 0px 1px;width:100%}.background-auto-size{width:100%}.weather-condition{font-size:27px}.main-forecast-info{font-size:18px}.main-temp{font-size:75px}.forecast{padding-top:100px}.weather-icon{width:85px}.forecast svg{width:60px}.forecast-temps{font-size:16px}}@media (max-width: 305px){.location{font-size:38px}.date{font-size:16px}.weather{padding:0px 1px 0px 1px;width:100%}.background-auto-size{width:100%}.weather-condition{font-size:25px}.main-forecast-info{font-size:15px}.main-temp{font-size:65px}.forecast{padding-top:90px}.weather-icon{width:70px}.forecast svg{width:50px}.forecast-temps{font-size:14px}}.app.normal{background-image:url(/weather_app_v2/static/media/normal.0ca2abb2.svg)}.app.night{background-image:url(/weather_app_v2/static/media/night.b0f70e02.svg)}.app.warm{background-image:url(/weather_app_v2/static/media/warm.ec541e8e.svg)}.app.hot{background-image:url(/weather_app_v2/static/media/hot.c07a921b.svg)}.desktop.normal{background:linear-gradient(to bottom, #331677, #dc38f5)}.desktop.night{background:linear-gradient(to bottom, #07193a, #599eb3)}.desktop.warm{background:linear-gradient(to bottom, #221e74, #b89a68)}.desktop.hot{background:linear-gradient(to bottom, #583e27, #c0a365)}*{margin:0;padding:0;box-sizing:border-box;color:white}body{font-family:"Montserrat", sans-serif}

